// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import axios from 'axios'
import App from './App'
import router from './router'
import store from './vuex/store'
import btx from './lib/Btx'

axios.defaults.baseURL = "http://47.92.92.207/api/";//接口请求根路径

Vue.config.productionTip = false
Vue.prototype.$axios = axios;
Vue.prototype.$B = btx;

/*全局工具方法*/
var login_url = "http://47.92.92.207/api/teacher_login/";

Vue.prototype.$utils = {

    /*权限登录地址*/
    login_url,

    /*资源集合*/
    resource: {},

    /*获取资源*/
    get_res(name, obj){
        if(this.resource[name]){//缓存资源
            obj[name] = this.resource[name];
        }else{
            axios.get("resource/")
                .then( res => {
                    res.data.forEach( r => {
                        if(r.tag === name){
                            this.resource[name] = obj[name] = r;//堆栈资源
                            return
                        }
                    })
                })
        }
    },

    /*合成发送时间*/
    gen_date_time(){
        let now = new Date();
        return now.toLocaleDateString().replace(/\//g, "-") + "T" + now.toLocaleTimeString("chinese", { hour12: false });
    }

};

/*http response 拦截器*/
axios.interceptors.response.use(
    undefined,
    error => {
        let res = error.response;
        switch (res.status) {
            case 403:
                console.log('您没有该操作权限');
                break;
            case 500:
                console.log('服务器错误');
        }
        return Promise.reject(error.response.data);//返回接口返回的错误信息
    });

/*路由监测*/
router.beforeEach((to, from, next) => {
    if (to.meta.check_login == true) {//页面需要用户登录
        if (store.state.user.id) {//已登陆
            next();
        } else {//未登陆
            next({path: "/"});
        }
    } else {//页面无需用户登录（登陆界面）
        if (store.state.user.id){
            router.push("/online_talk");//如果用户已登陆，直接跳转 “在线答疑”
        }else{
            next();
        }
    }
})

new Vue({
    el: '#app',
    router,
    store,
    components: {App},
    template: '<App/>'
})



