import Vue from "vue";
import Vuex from "vuex";
import createPersistedstate from "vuex-persistedstate";

Vue.use(Vuex);

export default new Vuex.Store({
    plugins: [
        createPersistedstate()
    ],
    state: {
        user: {
            id: "",
            intr: "",
            name: "",
            types: ""
        }
    },
    mutations: {

        /*用户登录*/
        login(state, val) {
            state.user = val;
        },

        /*退出登录*/
        exit_login(state){
            state.user.id = "";
            state.user.intr = "";
            state.user.name = "";
            state.user.types = "";
        }

    }
})


