<template>
    <div>
        <ul class="aside-nav-widget">
            <li class="an-w-btn" :state="data.act" v-for="data of init_data">

                <!--标准超链接-->
                <a v-if="data.type=='href'" :href="data.link" class="an-w-href" target="_blank">{{data.name}}</a>

                <!--单页面路由-->
                <router-link v-else-if="data.type=='route'" :to="data.link" class="an-w-href">{{data.name}}</router-link>

                <!--普通文本-->
                <span v-else>{{data.name}}</span>

            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "aside-nav-widget",
        props: [

            /*初始化构造数据*/
            "init_data"

        ],
    }
</script>

<style scoped>
    @import "../../css/aside-nav-widget.css";
</style>


