<template>
    <div>
        <matte-video-widget :video="matte_video"></matte-video-widget>
        <base-pannel-widget ref="basePannel" @insert_wiki="insert_wiki"></base-pannel-widget>
        <div class="pannel-widget">
            <h2 class="main-title-widget" style="padding: 0 2rem 2rem">
                <router-link class="mt-w-link" to="/online_talk/my_class?from=online_talk">我的班级</router-link>
                <span class="mt-w-text">{{cur_class.name}}</span>
            </h2>
            <h2 class="base-title-widget bt-w-blue">在线答疑</h2>
            <hr class="seg-widget"/>

            <!--对话区域-->
            <div class="grid" style="padding: 1.4rem 2rem">

                <!--通讯录-->
                <div class="col-4">
                    <tab-widget :tabs="tabs"></tab-widget>
                    <div class="pad-h-1"></div>
                    <div class="mail-list-widget">
                        <div class="ml-w-title">我的通讯录</div>
                        <ul class="ml-w-ul" v-if="group_mail_list.length > 0">
                            <li class="ml-w-li" v-for="mail of group_mail_list" :state="mail.state" :id="mail.id" @click="sel_group">
                                <head-widget :uname="mail.name.slice(0, 2)"></head-widget>
                                <span class="ml-w-name" v-text="mail.name"></span>
                            </li>
                        </ul>
                        <div v-else class="no-result-widget">
                            <span class="icon-nodata nr-w-ico"></span>
                            <span class="nr-w-text">该班级暂无分组</span>
                        </div>
                    </div>
                </div>

                <!--编辑器-->
                <div class="col-8" style="padding-left: 2rem">

                    <!--聊天记录-->
                    <div class="chat-record-widget" ref="chatRecord">
                        <ul v-if="chat_record.record.length > 0">
                            <li v-for="record of chat_record.record.slice(0,50)" :class="{'cr-w-item':true, 'cr-w-item-left':record.from === 'you', 'cr-w-item-right':record.from === 'me'}" @click="check_chat">
                                <div>
                                    <!--我发起-->
                                    <head-widget :uname="user.uname" :color="true" v-if="record.from === 'me'"></head-widget>
                                    <!--对方发起-->
                                    <head-widget :uname="record.from" v-else></head-widget>
                                </div>
                                <div class="cr-w-cont-box">
                                    <div class="cr-w-time">{{time_format(record.date_time)}}</div>
                                    <div class="cr-w-text">
                                        <div v-html="record.desc"></div>
                                        <video v-if="record.type == 'video'" controls
                                               class="cr-w-video">
                                            <source src="/static/res/video/video_2.mp4"/>
                                        </video>
                                    </div>
                                </div>
                            </li>
                        </ul>
                        <div v-else class="no-result-widget">
                            <span class="icon-nodata nr-w-ico"></span>
                            <span class="nr-w-text">暂无聊天记录</span>
                        </div>
                    </div>

                    <!--编辑器-->
                    <div style="padding-top: 4rem;">
                        <tiny-editor v-model="tinymce.html" :init="tinymce.setting"></tiny-editor>
                        <div style="text-align: right; padding: 1rem; border: 1px solid #ddd; border-top: none;">
                            <a class="base-btn-widget" @click="send_msg">发送消息</a>
                        </div>
                    </div>

                </div>
            </div>

        </div>
    </div>
</template>

<script>
    import Tab from "@/components/widget/tab-widget";
    import Head from "@/components/widget/head-widget";
    import basePannel from "@/components/widget/base-pannel-widget";
    import MatteVideo from "@/components/widget/matte-video-widget";
    import 'tinymce/tinymce'
    import 'tinymce/langs/zh_CN.js'
    import 'tinymce/themes/modern/theme'
    import Editor from '@tinymce/tinymce-vue'
    import 'tinymce/plugins/image'
    import 'tinymce/plugins/emoticons'
    import 'tinymce/plugins/emoticons'

    export default {
        name: "my_team_talk",
        data() {
            var _this = this;

            return {

                /*tab-widget*/
                tabs: [
                    {
                        name: "学生",
                        type: "route"
                    },
                    {
                        name: "小组",
                        type: "route",
                        state: "act"
                    },
                ],

                /*当前用户*/
                user: {
                    uid: "",
                    uname: ""
                },

                /*当前班级*/
                cur_class: {
                    id: "",
                    name: ""
                },

                /*当前班级全部小组*/
                group_mail_list: [],

                /*聊天记录*/
                chat_record: {
                    talk_to: {
                        tid: "",
                        name: ""
                    },
                    record: []
                },

                /*富文本编辑器*/
                tinymce: {
                    html: "",
                    setting: {
                        menu: {},
                        language: "zh_CN",
                        content_style: "img {max-width: 100%; height: auto}",
                        height: 157,
                        //document_base_url: 'http://www.geziwangshu.com/static/dist/',
                        skin_url: '/static/dist/teacher/tinymec/skins/lightgray',
                        plugins: 'image emoticons',
                        toolbar: 'image emoticons wiki',
                        branding: false,
                        statusbar: false,
                        content_css: "/static/dist/teacher/tinymec/skins/lightgray/wiki.css",
                        images_upload_url: "http://wyrun.com/btx/index/upload_img/",
                        setup: function(editor) {
                            editor.addButton('wiki', {
                                text: "Wiki",
                                onclick: function() {//显示wiki面板
                                    _this.toggle_wiki_pannel();
                                }
                            });
                        }
                    }
                },

                /*弹窗*/
                confirm: null,

                /*遮罩视频*/
                matte_video: {
                    state: "hide",
                    title: "",
                    video: ""
                }

            }
        },
        methods: {

            /*获取该班级全部小组*/
            get_group() {
                this.$axios.get("/group_class/?class=" + this.cur_class.id).then(res => {
                    this.group_mail_list = res.data;
                    this.init_msg();
                })
            },

            /*切换对话小组*/
            sel_group(e) {
                this.group_mail_list.forEach(mail => {
                    let id = mail.id;
                    if (mail.state === "act") mail.state = "";
                    if (id == e.currentTarget.id) {
                        mail.state = "act";
                        this.chat_record.talk_to = {//对话方
                            tid: id,
                            name: mail.name
                        }
                        this.chat_record.record = this.chat_record[id].msg;
                        this.top_msg();
                    }
                })
            },

            /*发送消息*/
            send_msg() {
                let msg = this.tinymce.html,
                    talk_to_tid = this.chat_record.talk_to.tid;
                if (talk_to_tid) {
                    let msg_tmp = msg.replace(/&nbsp;|<p>|<\/p>|\s/g, "");
                    if (msg_tmp.length === 0) {//输入非空验证
                        this.confirm.toast("无内容输入!", 2000, "notic");
                    } else {
                        let date_time = this.$utils.gen_date_time();
                        this.$axios.post("/group_teacher_msg_create/", {
                            message: msg,
                            send_time: date_time,
                            group: this.chat_record.talk_to.tid,
                            teacher: this.user.uid
                        }).then(res => {
                            this.chat_record.record.push({
                                "from": "me",
                                "desc": msg,
                                "date_time": date_time
                            });
                            this.tinymce.html = "";
                            this.top_msg();
                        })
                    }
                } else {
                    this.confirm.toast("请先选择通讯录小组", 2000, "notic");
                }
            },

            /*置顶聊天记录*/
            top_msg() {
                let $chat_record = this.$refs.chatRecord;
                if ($chat_record) {
                    this.$nextTick(function () {
                        $chat_record.scrollTop = $chat_record.scrollHeight;//置顶聊天记录
                    })
                }
            },

            /*初始化聊天记录*/
            init_msg(){
                this.group_mail_list.forEach(mail => {
                    this.chat_record[mail.id] = {
                        name: mail.name,
                        msg: []
                    }
                })
                this.$axios.all([
                    this.$axios.get("/group_teacher_stu_msg/?teacher=" + this.user.uid),
                    this.$axios.get("/group_teacher_msg/?teacher=" + this.user.uid),
                ]).then(this.$axios.spread((r1, r2) => {
                    r1.data.forEach(msg => {//对方发送的
                        this.chat_record[msg.group.id].msg.push({
                            "from": msg.user.name,
                            "desc": msg.message,
                            "date_time": msg.send_time,
                            "id": msg.id
                        });
                    })
                    r2.data.forEach(msg => {//我发送的
                        this.chat_record[msg.group.id].msg.push({
                            "from": "me",
                            "desc": msg.message,
                            "date_time": msg.send_time,
                            "id": msg.id
                        });
                    })

                    /*基于时间排序消息*/
                    this.group_mail_list.forEach(mail => {
                        this.chat_record[mail.id].msg.sort((fr,af) => {
                            if(fr.date_time < af.date_time) return -1;
                        })
                    })

                    this.top_msg();
                }))
            },

            /*格式化时间*/
            time_format(time) {
                let time_arr = time.substr(2).replace("T", " ").replace(/-/g, "/").split(":");
                time_arr.pop();
                return time_arr.join(":");
            },

            /*切换wiki面板*/
            toggle_wiki_pannel(){
                this.$refs.basePannel.toggle_wiki_pannel();
            },

            /*添加资源*/
            insert_wiki(data){
                let wiki,
                    type,
                    title;
                for(let src_id in data){
                    wiki = data[src_id];
                    type = wiki.type;
                    title = wiki.title;
                    if(type === "图片" || type === "导图"){
                        this.tinymce.html += "<p><img src='" + src_id + "' /></p>";
                    }else if(type === "音频" || type === "视频"){
                        this.tinymce.html += "<div class='cr-w-wiki' id='" + src_id + "' title='" + title + "'><div class='cr-w-wiki-player'></div>" + title + "</div><p></p>";
                    }
                }
            },

            /*查看消息资源*/
            check_chat(e) {
                let targ = e.target,
                    src;
                if(targ.nodeName.toLowerCase() === "img"){//图片资源
                    src = targ.src;
                    src && window.open(src, "_blank");
                }else if(targ.className === "cr-w-wiki"){//wiki多媒体资源
                    src = targ.getAttribute("id");
                    src && this.open_video_matte(targ.title, src);
                }
            },

            /*开启视频层*/
            open_video_matte(title, video){
                this.matte_video = {
                    state: "show",
                    title: title,
                    video: video
                };
            }

        },
        beforeMount(){
            this.cur_class.id = this.$route.query.class_id;
            this.cur_class.name = this.$route.query.name;
            this.tabs[0].link = "/online_talk/my_student_talk?class_id=" + this.cur_class.id + "&name=" + this.cur_class.name;
            this.tabs[1].link = "/online_talk/my_team_talk?class_id=" + this.cur_class.id + "&name=" + this.cur_class.name;
        },
        mounted() {
            this.confirm = this.$B.widgets.Confirm;
            this.cur_class.id = this.$route.query.class_id;
            this.cur_class.name = this.$route.query.name;
            this.user.uid = this.$store.state.user.id;
            this.user.uname = this.$store.state.user.name;
            this.get_group();

            /*定时刷新*/
            setInterval(() => {
                this.init_msg();
            }, 60000)

            this.$refs.chatRecord.style["max-height"] = window.innerHeight - 540 + "px";//动态设置聊天区域高度
        },
        components: {
            "head-widget": Head,
            "tab-widget": Tab,
            "base-pannel-widget": basePannel,
            "tiny-editor": Editor,
            "matte-video-widget": MatteVideo
        }
    }
</script>

<style scoped>
    @import "../../css/base-title-widget.css";
    @import "../../css/seg-widget.css";
    @import "../../css/pannel-widget.css";
    @import "../../css/main-title-widget.css";
    @import "../../css/head-widget.css";
    @import "../../css/search-widget.css";
    @import "../../css/mail-list-widget.css";
    @import "../../css/chat-record-widget.css";
    @import "../../css/no-result-widget.css";
    @import "../../css/base-btn-widget.css";
</style>


