import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'

//在线答疑
import onlineTalk from '@/components/online_talk'
import my_student_talk from '@/components/online_talk/my_student_talk'
import my_team_talk from '@/components/online_talk/my_team_talk'

//作业批改
import checkWork from '@/components/check_work'

//学员管理
import students from '@/components/students'
import my_student from '@/components/students/my_student'

import resource from '@/components/resource'
import tests from '@/components/tests'
import majors from '@/components/majors'

//我的班级
import my_class from '@/components/my_class'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            component: login
        },

        //在线答疑
        {
            path: '/online_talk',
            component: onlineTalk,
            meta: {
                check_login: true
            },
            children: [
                {
                    path: 'my_class',
                    component: my_class,
                    meta: {
                        check_login: true
                    }
                },
                {
                    path: 'my_student_talk',
                    component: my_student_talk,
                    meta: {
                        check_login: true
                    }
                },
                {
                    path: 'my_team_talk',
                    component: my_team_talk,
                    meta: {
                        check_login: true
                    }
                },
            ]
        },

        //作业批改
        {
            path: '/check_work',
            component: checkWork,
            meta: {
                check_login: true
            }
        },

        //学员管理
        {
            path: '/students',
            component: students,
            meta: {
                check_login: true
            },
            children: [
                {
                    path: 'my_class',
                    component: my_class,
                    meta: {
                        check_login: true
                    }
                },
                {
                    path: 'my_student',
                    component: my_student,
                    meta: {
                        check_login: true
                    }
                }
            ]
        },

        {
            path: '/resource',
            component: resource,
            meta: {
                check_login: true
            }
        },

        {
            path: '/tests',
            component: tests,
            meta: {
                check_login: true
            }
        },
        {
            path: '/majors',
            component: majors,
            meta: {
                check_login: true
            }
        },

        //我的班级
        {
            path: 'my_class',
            component: my_class,
            meta: {
                check_login: true
            }
        },
    ]
})



