<template>
  <div id="app">
    <matte-widget></matte-widget>       <!--遮罩-->
    <confirm-widget></confirm-widget>   <!--弹窗-->
    <nav-widget></nav-widget>           <!--导航-->
    <router-view></router-view>         <!--页面-->
  </div>
</template>

<script>
    import Nav from '@/components/widget/nav-widget';
    import Matte from '@/components/widget/matte-widget';
    import Confirm from '@/components/widget/confirm-widget';

    export default {
        name: 'App',
        components: {
            'nav-widget': Nav,
            'matte-widget': Matte,
            'confirm-widget': Confirm,
        }
    }
</script>

<style>
  @import "./css/btx.css";
  @import "./css/preset.css";
</style>



