<template>
    <div>
        <h2 class="main-title-widget" style="padding: 1rem 0 2rem">
            <router-link class="mt-w-link" to="/students/my_class?from=students">我的班级</router-link>
            <span class="mt-w-text">{{cur_class.name}}</span>
        </h2>

        <div class="pannel-widget">
            <h2 class="base-title-widget bt-w-blue">学生管理</h2>
            <hr class="seg-widget"/>
            <div class="pad-h-1"></div>

            <!--搜索栏组件-->
            <div class="grid" style="padding: 0 0 2rem 2rem">
                <div class="col-5">
                    <div class="search-widget">
                        <div class="s-w-inp">
                            <i class="s-w-inp-ico icon-sousuo"></i>
                            <input class="s-w-inp-text" type="text" placeholder="学员搜索 ..." v-model="serch_uname"/>
                        </div>
                        <span class="s-w-btn" @click="search_student">搜索</span>
                    </div>
                </div>
                <div class="col-7"></div>
            </div>

            <!--排行榜组件-->
            <div class="base-table-widget">
                <table class="bt-w-table">
                    <thead>
                    <tr class="bt-w-title">
                        <td class="bt-w-title-td">姓名</td>
                        <td class="bt-w-title-td pcenter">进度</td>
                        <td class="bt-w-title-td pcenter">当前任务</td>
                        <td class="bt-w-title-td pcenter">小组</td>
                        <td class="bt-w-title-td pcenter">周进度</td>
                        <td class="bt-w-title-td pcenter">月进度</td>
                        <td class="bt-w-title-td pcenter">操作</td>
                    </tr>
                    </thead>
                    <tbody>
                    <tr class="bt-w-tr" v-for="(student, index) of show_students">
                        <td class="bt-w-td">
                            <head-widget :uname="student.name"></head-widget>
                            <span>{{student.name}}</span>
                        </td>
                        <td class="bt-w-td pcenter">{{student.progress}}</td>
                        <td class="bt-w-td pcenter">{{student.tesk}}</td>
                        <td class="bt-w-td pcenter">{{student.in_class.name}}</td>
                        <td class="bt-w-td pcenter">{{student.week_progress}}</td>
                        <td class="bt-w-td pcenter">{{student.month_progress}}</td>
                        <td class="bt-w-td pcenter"><span>以学生身份访问</span></td>
                    </tr>
                    </tbody>
                </table>
            </div>

            <!--页码组件-->
            <!--<div class="page-widget" style="margin-bottom: 1rem;">
                <a class="p-w-btn" href="#"><i class="icon-arrow_left"></i></a>
                <span class="p-w-number"><b class="p-w-cur">{{article_pages.cur_page}}</b>/<small class="p-w-total">{{article_pages.total_pages}}</small></span>
                <a href="#" class="p-w-btn"><i class="icon-arrow_right"></i></a>
            </div>-->

        </div>
    </div>
</template>

<script>
    import Head from "@/components/widget/head-widget";

    export default {
        name: "my_student",
        data(){
            return {

                /*当前班级*/
                cur_class: {
                    id: "",
                    name: ""
                },

                /*搜索用户名*/
                serch_uname: "",

                /*全部学员列表*/
                students: [],

                /*显示学员列表*/
                show_students: [],

                /*分页*/
                article_pages: {
                    cur_page: 1,
                    total_pages: 12
                }

            }
        },
        methods: {

            /*搜索学员*/
            search_student() {
                if (!this.serch_uname) {//无搜索字段，返回全部
                    this.show_students = this.students;
                } else {
                    this.show_students = [];
                    this.students.forEach(student => {
                        if (student.name.search(this.serch_uname) > -1) {//有结果（模糊搜索）
                            this.show_students.push(student);
                            return;
                        }
                    })
                }
                this.serch_uname = "";
            },

            /*获取该班级全部学员*/
            get_students() {
                this.$axios.get("student/?class=" + this.cur_class.id).then(res => {
                    this.show_students = this.students = res.data;
                })
            },

        },
        beforeMount(){
            this.cur_class.id = this.$route.query.class_id;
            this.cur_class.name = this.$route.query.name;
        },
        mounted(){
            this.get_students();
        },
        components: {
            "head-widget": Head
        }
    }
</script>

<style scoped>
    @import "../../css/seg-widget.css";
    @import "../../css/base-title-widget.css";
    @import "../../css/pannel-widget.css";
    @import "../../css/search-widget.css";
    @import "../../css/base-table-widget.css";
    @import "../../css/page-widget.css";
    @import "../../css/main-title-widget.css";
</style>


