<template>
    <div class="base-pannel-widget" :state="wiki_pannel.state">
        <matte-video-widget :video="matte_video"></matte-video-widget>
        <div class="bp-w-title">Wiki·资源库</div>
        <div class="bp-w-head">
            <div class="search-widget">
                <div class="s-w-inp">
                    <input type="text" placeholder="搜索资源" class="s-w-inp-text" maxlength="6"
                           v-model="wiki_pannel.serch">
                </div>
                <span class="s-w-btn" @click="serch_wiki"><span
                        class="icon-sousuo s-w-sousuo-ico"></span></span>
            </div>
        </div>
        <div class="bp-w-box" @click="sel_wiki_tab">
            <a class="bp-w-tab" :state="tab.state" :data-index="index"
               v-for="(tab, index) of wiki_pannel.tabs">{{tab.name}}</a>

        </div>
        <div class="bp-w-box">
            <div class="bp-w-ul">
                <div class="base-table-widget" v-if="wiki_pannel.list.length > 0">
                    <table class="bt-w-table">
                        <thead>
                        <tr class="bt-w-title">
                            <td class="bt-w-title-td">类型</td>
                            <td class="bt-w-title-td pcenter">内容</td>
                            <td class="bt-w-title-td">标题</td>
                        </tr>
                        </thead>
                        <tbody @click="sel_res">
                        <tr class="bt-w-tr" :id="item.src" :type="item.type" :title="item.title" v-for="item of wiki_pannel.list">
                            <td class="bt-w-td">{{item.type}}</td>
                            <td class="bt-w-td pcenter">

                                <!--图片、导图-->
                                <a target="_blank" :href="item.src" v-if="item.type === '图片' || item.type === '导图'">
                                    <img :src="item.image" class="bt-w-img" />
                                </a>

                                <!--视频、音频-->
                                <a @click="open_video_matte(item.title, item.src)" v-if="item.type === '视频' || item.type === '音频'">
                                    <i class="icon-bofang bt-w-ico-wenjian"></i>播放
                                </a>

                            </td>
                            <td class="bt-w-td">{{item.title}}</td>
                        </tr>
                        </tbody>
                    </table>
                </div>
                <div v-else class="no-result-widget">
                    <span class="icon-nodata nr-w-ico"></span>
                    <span class="nr-w-text">暂无该wiki资源</span>
                </div>
            </div>
        </div>
        <div class="bp-w-bottom-btn">
            <span class="base-btn-widget" @click="insert_wiki">确定</span>
            <span class="base-btn-widget" state="visied" @click="toggle_wiki_pannel">取消</span>
        </div>
    </div>
</template>

<script>
    import MatteVideo from "@/components/widget/matte-video-widget";

    export default {
        name: "base-pannel-widget",
        props: ["wiki-state"],
        data() {
            return {

                /*wiki面板*/
                wiki_pannel: {
                    state: "hide",
                    serch: "",
                    tabs: [
                        {
                            name: "图片",
                            tag: "IMG",
                            state: "act"
                        },
                        {
                            name: "视频",
                            tag: "VIDEO",
                            state: "act"
                        },
                        {
                            name: "导图",
                            tag: "XMIND",
                            state: "act"
                        },
                        {
                            name: "音频",
                            tag: "AUDIO",
                            state: "act"
                        }
                    ],
                    list: [],
                    selected: {}
                },

                /*搜索结果*/
                search_result: false,

                /*遮罩视频*/
                matte_video: {
                    state: "hide",
                    title: "",
                    video: ""
                }

            }
        },
        methods: {

            /*切换wiki面板*/
            toggle_wiki_pannel() {
                let matte = this.$B.widgets.Matte;
                if (this.wiki_pannel.state === "show") {//关闭
                    this.wiki_pannel.state = "hide";
                    matte.hide();
                } else {//显示
                    this.wiki_pannel.state = "show";
                    this.wiki_pannel.list = [];
                    matte.show();
                }
            },

            /*选择wiki_tab*/
            sel_wiki_tab(e) {
                let targ = e.target,
                    cur_tab = this.wiki_pannel.tabs[targ.dataset.index];
                if (targ.nodeName.toLowerCase() === "a") cur_tab.state = cur_tab.state === "act" ? "" : "act";
            },

            /*搜索wiki*/
            serch_wiki() {
                let serch_name = this.wiki_pannel.serch;
                if (serch_name) {//无搜索字段，返回全部
                    this.$axios.get("library_search_tag/?search=" + this.wiki_pannel.serch)
                        .then(res => {
                            if (res.data.length > 0) {
                                this.search_result = {
                                    IMG: [],
                                    VIDEO: [],
                                    XMIND: [],
                                    AUDIO: []
                                }
                                res.data.forEach(d => {

                                    /*图片资源*/
                                    this.search_result["IMG"] = this.search_result["IMG"].concat(this.$B.data_convert(d.images, {
                                        id: "id",
                                        type: ":图片",
                                        title: "name",
                                        src: "image",
                                    }));

                                    /*音频资源*/
                                    this.search_result["AUDIO"] = this.search_result["AUDIO"].concat(this.$B.data_convert(d.audios, {
                                        id: "id",
                                        type: ":音频",
                                        title: "name",
                                        src: "image",
                                    }));

                                    /*导图资源*/
                                    this.search_result["XMIND"] = this.search_result["XMIND"].concat(this.$B.data_convert(d.minds, {
                                        id: "id",
                                        type: ":导图",
                                        title: "name",
                                        src: "mind_image",
                                    }));

                                    /*视频资源*/
                                    this.search_result["VIDEO"] = this.search_result["VIDEO"].concat(this.$B.data_convert(d.videos, {
                                        id: "id",
                                        type: ":视频",
                                        title: "name",
                                        src: "video_url",
                                    }));

                                })

                                /*标签过滤*/
                                this.wiki_pannel.list = [];
                                this.wiki_pannel.tabs.forEach(tab => {
                                    if(tab.state === "act") this.wiki_pannel.list = this.wiki_pannel.list.concat(this.search_result[tab.tag]);
                                })

                            } else {//无搜索结果
                                this.wiki_pannel.list = [];
                                this.search_result = false;
                            }
                        })
                }
                this.wiki_pannel.serch = "";
            },

            /*开启视频层*/
            open_video_matte(title, video){
                this.matte_video = {
                    state: "show",
                    title: title,
                    video: video
                };
            },

            /*选择wiki资源*/
            sel_res(e){
                let targ = e.target.parentNode;
                if(targ.getAttribute("state") === "act"){//取消
                    targ.setAttribute("state", "");
                    delete this.wiki_pannel.selected[targ.id];
                }else{//选中
                    targ.setAttribute("state", "act");
                    this.wiki_pannel.selected[targ.id] = {
                        type: targ.getAttribute("type"),
                        title: targ.getAttribute("title")
                    };
                }
            },

            /*添加资源*/
            insert_wiki(){
                this.toggle_wiki_pannel();
                this.$emit("insert_wiki", this.wiki_pannel.selected);
                this.wiki_pannel.selected = {};
            }

        },
        components: {
            "matte-video-widget": MatteVideo
        }
    }
</script>

<style scoped>
    @import "../../css/base-title-widget.css";
    @import "../../css/seg-widget.css";
    @import "../../css/pannel-widget.css";
    @import "../../css/search-widget.css";
    @import "../../css/no-result-widget.css";
    @import "../../css/base-btn-widget.css";
    @import "../../css/base-pannel-widget.css";
    @import "../../css/base-table-widget.css";
</style>


