<template>
    <div>
        <div class="learn-outline-widget">
            <dl class="lo-w-list pannel-widget">
                <dt class="base-title-widget"><h3>我的班级</h3></dt>
                <dd class="grid lo-w-list-items">
                    <div class="col-3" v-for="(clas, index) of classes">
                        <router-link :to="next_router + '?class_id=' + clas.id + '&name=' + clas.name" class="lo-w-list-item lo-w-list-item-btn">
                            <div class="lo-w-list-item-cover">班级 {{index + 1}}</div>
                            <div class="lo-w-list-item-name">{{clas.name}}</div>
                        </router-link>
                    </div>
                </dd>
            </dl>
        </div>
    </div>
</template>

<script>
    export default {
        name: "online_talk",
        data() {
            return {

                /*下级路由*/
                next_router: "",

                /*老师所带班级*/
                classes: [],

            }
        },
        methods: {

            /*获取班级*/
            get_class(){
                this.$axios.get("class/?teacher=" + this.$store.state.user.id)
                    .then(res => {
                        this.classes = res.data;
                    })
            }

        },
        mounted(){
            if(this.$route.query.from === "students"){//学员管理
                this.next_router = "/students/my_student"
            }else{//在线答疑
                this.next_router = "/online_talk/my_student_talk"
            }
            this.get_class();
        },
    }
</script>

<style scoped>
    @import "../css/learn-outline-widget.css";
    @import "../css/base-title-widget.css";
    @import "../css/pannel-widget.css";
</style>


