<template>
    <div class="matte-video-widget" :state="matte_video.state" @dblclick="close_video_matte">
        <div class="mv-w-box">
            <span class="mv-w-close" @click="close_video_matte">关闭</span>
            <div class="mv-w-title">{{matte_video.title}}</div>
            <video controls :src="matte_video.video"></video>
        </div>
    </div>
</template>

<script>
    export default {
        name: "matte-video-widget",
        props: ["video"],
        data(){
            return {

                /*播放器*/
                matte_video: {}

            }
        },
        methods: {

            /*视频层状态*/
            close_video_matte(){
                this.matte_video = {
                    state: "hide",
                    title: "",
                    video: ""
                };
            }

        },
        watch: {
            video(params){
                this.matte_video = params;
            }
        }
    }
</script>

<style scoped>
    @import "../../css/matte-video-widget.css";
</style>


