<template>
    <div class="nav-widget">
        <div class="container n-w-box grid">
            <div class="n-w-left">
                <img class="n-w-logo" :src="logo_src.image"/>
            </div>
            <div class="n-w-right">
                <span v-if="user.id==''">
                    <router-link to="/" state="login" class="head-widget">
                        <span class="icon-login h-w-login-ico"></span>
                    </router-link>
                    <router-link to="/" >登录</router-link>
                </span>
                <span v-else>
                    <head-widget :uname="user.name"></head-widget>
                    <a @click="exit_login">退出</a>
                </span>
            </div>
        </div>
    </div>
</template>

<script>
    import Head from "@/components/widget/head-widget"

    export default {
        name: 'nav-widget',
        data() {
            return {

                /*logo*/
                logo_src: "",

            }
        },
        beforeMount(){
            this.$utils.get_res("logo_src", this);
        },
        methods: {

            /*退出登录*/
            exit_login(){
                this.$store.commit("exit_login");
                this.$router.push("/");
            }

        },
        computed: {

            /*用户信息*/
            user() {
                return this.$store.state.user;
            }

        },
        components: {
            "head-widget": Head,
        }
    }
</script>

<style scoped>
    @import "../../css/nav-widget.css";
    @import "../../css/head-widget.css";
</style>


