<template>
    <span :class="{'head-widget':true, 'h-w-green':color}">{{short_uname}}<span class="h-w-msg" v-if="msg_count">{{msg_count}}</span></span>
</template>

<script>
    export default {
        name: "head-widget",
        props: ["uname", "color", "msg-count"],
        computed: {

            /*显示名字最后2个字*/
            short_uname() {
                return this.uname.substr(-2, 2);
            },

            /*显示未读消息*/
            msg_count(){
                if(this.msgCount > 10){
                    return "···";
                }else if(this.msgCount > 0){
                    return this.msgCount;
                }else{
                    return "";
                }
            }

        },
    }
</script>

<style scoped>
    @import "../../css/head-widget.css";
</style>


