<template>
    <div class="matte-widget" :state="this.matte.state" :style="{'z-index':this.matte.zindex, 'color':this.matte.color}"></div>
</template>

<script>
    export default {
        name: "matte-widget",
        data(){
            return {

                /*遮罩状态*/
                matte: {
                    state: "hidden",
                    color: "",
                    zindex: "",
                }

            }
        },
        methods: {

            /*显示遮罩*/
            show(zindex, color){
                this.matte = {
                    state: "show",
                    color: color,
                    zindex: zindex,
                }
            },

            /*关闭遮罩*/
            hide(){
                this.matte = {
                    state: "hidden",
                    color: "",
                    zindex: "",
                }
            }

        },
        mounted(){
            this.$B.widgets.regist("Matte", this);
        }
    }
</script>

<style scoped>
    @import "../../css/matte-widget.css";
</style>


