<template>
    <div>
        <matte-video-widget :video="matte_video"></matte-video-widget>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">教师端</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10" style="padding-bottom: 5rem">
                <div class="pannel-widget" style="padding: 1.7rem 2rem;">
                    <div class="grid">
                        <div class="col-4">
                            <tab-widget :tabs="tabs"></tab-widget>
                        </div>
                        <div class="col-3"></div>
                        <div class="col-5" style="text-align: right">
                            <div class="search-widget">
                                <div class="s-w-inp">
                                    <input type="text" placeholder="搜索学员作业" class="s-w-inp-text"
                                           maxlength="6" v-model="serch_uname">
                                </div>
                                <span class="s-w-btn" @click="serch_user_works"><span
                                        class="icon-sousuo s-w-sousuo-ico"></span></span>
                            </div>
                        </div>
                    </div>
                </div>

                <div v-if="show_works.length > 0">
                    <div class="pannel-widget" style="margin-top: 1rem; padding-top: 1.7rem" v-for="work of show_works.slice(0, 10)">
                        <h2 class="base-title-widget bt-w-blue">
                            <head-widget :uname="work.user.name"></head-widget>
                            <span class="bt-w-sub-text">{{work.data_info}}</span>
                        </h2>
                        <hr class="seg-widget"/>
                        <div class="grid" style="padding-top: 2rem">
                            <div class="col-7" style="padding: 0 2rem;">
                                <div class="list-text-widget grid">
                                    <div class="col-12" v-if="work.image">
                                        <div class="lt-w-item">
                                            <a class="lt-w-item-img-box" target="_blank"
                                               :href="work.image">
                                                <img class="lt-w-item-img" :src="work.image"/>
                                            </a>
                                        </div>
                                    </div>
                                    <div class="col-12">
                                        <div class="lt-w-item">
                                            <span>提交时间</span>
                                            <span class="lt-w-item-after">{{time_format(work.submit_time)}}</span>
                                        </div>
                                    </div>
                                    <div class="col-6">
                                        <div class="lt-w-item">
                                            <span>所属课程</span>
                                            <span class="lt-w-item-after">{{work.chapter.course_name.name}}</span>
                                        </div>
                                    </div>
                                    <div class="col-6">
                                        <div class="lt-w-item">
                                            <span>所属章节</span>
                                            <span class="lt-w-item-after">{{work.chapter.chapter_name}}</span>
                                        </div>
                                    </div>
                                    <div class="col-6">
                                        <div class="lt-w-item">
                                            <span>作业文件</span>
                                            <span class="lt-w-item-after">
                                                <a class="lt-w-item-after-ico" :href="work.file"
                                                   download v-if="work.file"><i
                                                        class="icon-xiazai"></i>下载</a>
                                                <span v-else>无提交文件</span>
                                            </span>
                                        </div>
                                    </div>
                                    <div class="col-6">
                                        <div class="lt-w-item">
                                            <span>作业视频</span>
                                            <span class="lt-w-item-after">
                                                <a class="lt-w-item-after-ico" href="####"
                                                   @click="open_video_matte(work.chapter.chapter_name + ' -> ' + work.mission.name, work.video_file)"
                                                   v-if="work.video_file"><i
                                                        class="icon-bofang"></i>播放</a>
                                                <span v-else>无提交视频</span>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="col-5">
                                <h2 class="base-title-widget">
                                    <span>点评作业</span>
                                    <div class="bt-w-right">
                                        <ul class="tab-widget" @click="sel_pass(work, $event)">
                                            <li class="t-w-item"><span class="t-w-item-btn" :state="work.task_end == true?'pass':''">通过</span></li>
                                            <li class="t-w-item"><span class="t-w-item-btn" :state="work.task_end == false?'unpass':''">未过</span></li>
                                        </ul>
                                    </div>
                                </h2>
                                <div style="padding-right: 2rem; margin-top: 2.7rem; padding-bottom: 2rem">
                                    <tiny-editor v-model="work.comment" :init="tinymce.setting"></tiny-editor>
                                    <div style="text-align: center; padding: 1rem; background-color: #eee; border: 1px solid #ddd; border-top: none;">
                                        <a class="base-btn-widget" @click="correct_work(work)">评改</a>
                                    </div>
                                </div>

                                <!--任务点评历史记录-->
                                <h2 class="base-title-widget">
                                    <a @click="check_task_comment(work)">查看作业点评</a>
                                </h2>
                                <div v-if="work.comment_records">
                                    <div v-if="work.comment_records.length > 0">
                                        <div style="padding-right: 2rem;" v-for="comment of work.comment_records">
                                            <div class="basic-article-widget">
                                                <p class="ba-w-p" v-html="comment.data"></p>
                                                <p class="ba-w-small-p pright"><span class="text-strong">{{comment.evaluation_time}}</span></p>
                                            </div>
                                            <hr state="max_line" class="seg-widget">
                                        </div>
                                    </div>
                                    <div v-else class="no-result-widget">
                                        <span class="icon-nodata nr-w-ico"></span>
                                        <span class="nr-w-text">该作业暂无点评!</span>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <div class="pannel-widget no-result-widget" style="margin-top: 1rem;" v-else>
                    <span class="icon-nodata nr-w-ico"></span>
                    <span class="nr-w-text">暂无作业</span>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";
    import Tab from "@/components/widget/tab-widget";
    import HeadWidget from "@/components/widget/head-widget";
    import MatteVideo from "@/components/widget/matte-video-widget";
    import 'tinymce/tinymce'
    import 'tinymce/langs/zh_CN.js'
    import 'tinymce/themes/modern/theme'
    import 'tinymce/plugins/emoticons'
    import Editor from '@tinymce/tinymce-vue'

    export default {
        name: "check_work",
        data() {
            var _this = this;

            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "在线答疑",
                        type: "route",
                        link: "/online_talk/my_class?from=online_talk",
                    },
                    {
                        name: "作业批改",
                        type: "route",
                        act: "act",
                        link: "/check_work",
                    },
                    {
                        name: "学生管理",
                        type: "route",
                        link: "/students/my_class?from=students",
                    },
                    {
                        name: "资源管理",
                        type: "route",
                        link: "/resource",
                    },
                    {
                        name: "试题管理",
                        type: "route",
                        link: "/tests",
                    },
                    {
                        name: "课程管理",
                        type: "route",
                        link: "/majors",
                    }
                ],

                /*tab-widget*/
                tabs: [
                    {
                        name: "未批改",
                        state: "act",
                        callback() {
                            _this.show_works_by_read("unread");
                        }
                    },
                    {
                        name: "已批改",
                        state: "",
                        callback() {
                            _this.show_works_by_read("readed");
                        }
                    }
                ],

                /*全部作业列表*/
                works: [],

                /*未批改作业列表*/
                unread_works: [],

                /*已批改作业列表*/
                readed_works: [],

                /*显示作业分类状态*/
                show_work_state: "unread",

                /*显示作业列表*/
                show_works: [],

                /*遮罩视频*/
                matte_video: {
                    state: "hide",
                    title: "",
                    video: ""
                },

                /*富文本编辑器*/
                tinymce: {
                    setting: {
                        menu: {},
                        language: "zh_CN",
                        content_style: "img {max-width: 100%; height: auto}",
                        height: 110,
                        skin_url: '/static/dist/teacher/tinymec/skins/lightgray',
                        plugins: "emoticons",
                        toolbar: 'bold italic underline strikethrough emoticons',
                        branding: false,
                        statusbar: false
                    }
                },

                /*搜索学员名*/
                serch_uname: "",

                /*confirm*/
                confirm: null

            }
        },
        methods: {

            /*基于学员搜索作业*/
            serch_user_works() {
                let works = this[this.show_work_state + "_works"];
                if (this.serch_uname) {//有搜索
                    this.show_works = [];
                    works.forEach(work => {
                        if (work.user.name === this.serch_uname) {//有结果
                            this.show_works.push(work);
                        }
                    })
                } else {//无搜索（显示全部）
                    this.show_works = works;
                }
                this.serch_uname = "";
            },

            /*格式化时间*/
            time_format(time) {
                let time_arr = time.substr(2).replace("T", " ").replace(/-/g, "/").split(":");
                time_arr.pop();
                return time_arr.join(":");
            },

            /*获取该班级全部作业*/
            get_all_works() {
                this.confirm.load("作业获取中!");
                this.unread_works = [];
                this.readed_works = [];
                this.$axios.get("teacher_practice_all/?teacher=" + this.$store.state.user.id).then(res => {
                    let works = res.data.results;
                    works.sort((a, b) => {
                        if (a.submit_time > b.submit_time) return -1;
                    })

                    /*作业分类*/
                    works.forEach(work => {
                        work.comment = "";
                        work.comment_records = null;
                        if (work.task_status == 1) {//未批改
                            this.unread_works.push(work);
                        } else if (work.task_status == 2) {//已批改
                            this.readed_works.push(work);
                        }
                    })

                    this.works = works;
                    this.show_works = this.unread_works;//默认显示未批改作业
                    this.confirm.load("作业获取成功!", "end");
                })
            },

            /*选择作业通过状态*/
            sel_pass(work, e) {
                if (e.target.innerHTML === "通过") {
                    work.task_end = true;
                } else if (e.target.innerHTML === "未过") {
                    work.task_end = false;
                }
            },

            /*评改作业*/
            correct_work(work) {
                let comment_tmp = work.comment.replace(/&nbsp;|<p>|<\/p>|\s/g, "");
                if (comment_tmp.length === 0) {//输入非空验证
                    this.confirm.toast("请输入点评内容!", 2000, "notic");
                    return;
                }else{
                    this.confirm.load("作业点评中!");
                    this.$axios.all([
                        this.$axios.patch("user_mission_status/" + work.id + "/", {
                            task_status: 2
                        }),
                        this.$axios.post("teacher_evaluation/", {
                            data: work.comment,
                            user_mission_id: work.id,
                            evaluation_time: this.$utils.gen_date_time(),
                            is_pass: work.task_end?2:1,
                            teacher: this.$store.state.user.id,
                            mission: work.mission.id,
                            user: work.user.id
                        })
                    ]).then(this.$axios.spread((r1, r2) => {
                        work.task_status = 2;//设置作业状态为已批改

                        /*判定该作业是否通过*/
                        this.$axios.put("user_mission_end/" + work.id + "/", {
                            task_end: work.task_end,
                        }).then(res => {
                            this.confirm.load("作业已批改!", "end");
                            this.get_all_works();
                            this.tabs[0].state = "act";
                            this.tabs[1].state = "";
                        })

                    }))
                }
            },

            /*查看任务点评*/
            check_task_comment(work){
                this.$axios.get("teacher_evaluation_user_task/?user_task=" + work.id).then(res => {
                    work.comment_records = res.data;
                    res.data.forEach( data => {
                        data.evaluation_time = this.time_format(data.evaluation_time);
                    });
                    work.comment_records.sort((a,b) => {
                        if(a.evaluation_time > b.evaluation_time) return -1;
                    })
                })
            },

            /*切换显示作业分类*/
            show_works_by_read(type) {
                this.show_work_state = type;
                this.show_works = this[type + "_works"];
            },

            /*开启视频层*/
            open_video_matte(title, video) {
                this.matte_video = {
                    state: "show",
                    title: title,
                    video: video
                };
            },

        },
        mounted() {
            this.confirm = this.$B.widgets.Confirm;
            this.get_all_works();
        },
        components: {
            "head-widget": HeadWidget,
            "tab-widget": Tab,
            "aside-nav-widget": AsideNav,
            "matte-video-widget": MatteVideo,
            "tiny-editor": Editor
        }
    }
</script>

<style scoped>
    @import "../css/seg-widget.css";
    @import "../css/base-title-widget.css";
    @import "../css/pannel-widget.css";
    @import "../css/base-table-widget.css";
    @import "../css/base-btn-widget.css";
    @import "../css/list-text-widget.css";
    @import "../css/no-result-widget.css";
    @import "../css/search-widget.css";
    @import "../css/tab-widget.css";
    @import "../css/basic-article-widget.css";
</style>


