<template>
    <div class="confirm-widget" :state="pannel.state">
        <div class="c-w-cont">
            <i class="c-w-ico" :state="pannel.icon_state" v-show="pannel.icon_state"></i>
            <span class="c-w-text">{{pannel.text}}</span>
        </div>
        <div class="c-w-btns" v-if="pannel.mode === 'confirm'">
            <a class="c-w-btn" type="cancel">取消</a><a class="c-w-btn" type="ok">确认</a>
        </div>
    </div>
</template>

<script>
    export default {
        name: "confirm-widget",
        data(){
            return {

                /*弹窗状态*/
                pannel: {
                    state: "hidden",
                    mode: "",
                    text: "",
                    icon_state: ""
                },

                /*遮罩层*/
                matte: null

            }
        },
        methods: {

            /*显示提示弹窗*/
            toast(text, duration, icon_state){
                if(this.pannel.state !== "show"){
                    this.matte && this.matte.show();
                    this.pannel = {
                        state: "show",
                        mode: "toast",
                        text: text
                    };
                    if(duration){
                        setTimeout(()=>{
                            this.close();
                        }, duration)
                    }
                    this.pannel.icon_state = icon_state || "";
                }
            },

            /*显示加载弹窗*/
            load(text, method){
                if(method === "end"){//关闭弹窗
                    this.pannel.text = text;
                    this.pannel.icon_state = "success";
                    setTimeout(()=>{
                        this.close();
                    }, 1000)
                }else{//开启弹窗
                    this.matte && this.matte.show();
                    this.pannel = {
                        state: "show",
                        mode: "toast",
                        text: text,
                        icon_state: "loading"
                    };
                }
            },

            /*关闭窗口*/
            close(){
                this.matte && this.matte.hide();
                this.pannel = {
                    state: "hidden",
                    mode: "",
                    text: "",
                    icon_state: ""
                };
            }

        },
        mounted(){
            this.matte = this.$B.widgets.Matte;
            this.$B.widgets.regist("Confirm", this);
        }
    }
</script>

<style scoped>
    @import "../../css/confirm-widget.css";
</style>


