<template>
    <div>
        <ul class="tab-widget" @click="tab_sel">
            <li class="t-w-item" v-for="(tab, index) of tabs">
                <router-link class="t-w-item-btn" :state="tab.state" :index="index" :to="tab.link" v-if="tab.type === 'route'">{{tab.name}}</router-link>
                <span class="t-w-item-btn" :state="tab.state" :index="index" v-else>{{tab.name}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "tab-widget",
        props: ["tabs"],
        methods: {

            /*标签触发*/
            tab_sel(e) {
                let cur_index;
                if (cur_index = e.target.getAttribute("index")) {
                    this.tabs.forEach(function (tab, index) {
                        if (tab.state === "act") tab.state = "";
                        if (index == cur_index) {
                            tab.callback && tab.callback();//执行回调函数
                            tab.state = "act";
                        }
                    })
                }
            }

        }
    }
</script>

<style scoped>
    @import "../../css/tab-widget.css";
</style>


