<template>
    <div class="login-widget">
        <div class="l-w-title">
            <p><img :src="logo_simple_src.image"/></p>
            <p class="l-w-title-text"><span>格子网塾平台 - 教师登陆</span></p>
        </div>
        <div class="l-w-inp-item">
            <span class="icon-uname l-w-inp-item-ico"></span>
            <span class="l-w-inp-item-text">账号</span>
            <span class="l-w-inp-item-inp">
                <input type="text" placeholder="请输入账号" maxlength="16" v-model="login_info.uname" />
            </span>
        </div>
        <div class="l-w-inp-item">
            <span class="icon-upwd l-w-inp-item-ico"></span>
            <span class="l-w-inp-item-text">密码</span>
            <span class="l-w-inp-item-inp">
                <input type="password" placeholder="请输入密码" maxlength="12" v-model="login_info.upwd" />
            </span>
        </div>
        <div class="pad-h-2"></div>
        <div class="pcenter">
            <span state="normal" class="base-btn-widget" @click="user_login">登陆</span>
        </div>
    </div>
</template>

<script>
    export default {
        name: "login",
        data(){
            return {

                /*登录信息*/
                login_info: {
                    uname: "",
                    upwd: "",
                },

                /*logo简图*/
                logo_simple_src: "",

                /*confirm*/
                confirm: null

            }
        },
        methods: {

            /*登陆请求*/
            user_login(){
                let formdata = new FormData();
                formdata.append('name', this.login_info.uname);
                formdata.append('password', this.login_info.upwd);
                this.$axios({
                    method: 'post',
                    url: this.$utils.login_url,
                    data: formdata,
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(res => {
                    let data = res.data;
                    if(data.id){//登录成功
                        this.$store.commit("login", {
                            id: data.id,
                            intr: data.intr,
                            name: data.name,
                            types: data.types
                        });
                        this.$router.push("/online_talk/my_class");
                    }else{
                        this.confirm.toast("账号或密码有误!", 3000, "notic")
                    }
                })
            }

        },
        mounted(){
            this.confirm = this.$B.widgets.Confirm;
            this.$utils.get_res("logo_simple_src", this);
        }
    }
</script>

<style scoped>
    @import "../css/login-widget.css";
    @import "../css/base-btn-widget.css";
</style>


